@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700);
@import url(http://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
body {
  background: #5FA292;
  width: 100%;
  height: 100%; }

.wrapper {
  margin: 1%;
  position: absolute;
  background: white;
  width: 98%;
  height: auto;
  border-radius: 10px; }

#main-photo {
  margin: 0;
  padding: 0;
  position: absolute;
  min-height: 20%;
  width: 30%;
  top: 18px;
  left: 15px;
  border-radius: 10px; }

.rating-box > * {
  position: absolute;
  display: table-cell;
  vertical-align: middle;
  top: 23%;
  padding: 0.75em 20px; }

.rating-box input[type="radio"] {
  display: none; }

.rating-box input[type="radio"]:checked ~ label::before {
  color: orange; }

.rating-box label {
  display: inline-block;
  background-color: transparent;
  cursor: pointer; }

.rating-box label::before {
  content: "\2605";
  font-family: traductorium;
  font-size: 40px;
  color: grey; }

.rating-box label:hover::before {
  color: green; }

.rating-box label:hover ~ label::before {
  color: green; }

.rating-labels {
  direction: rtl; }

.user-info {
  position: relative;
  left: 35%; }

#name {
  margin-top: 10%;
  font-size: 40px; }

.user-info input {
  display: inline;
  font-size: 16px;
  border: none;
  border-bottom: 1px solid black;
  margin-bottom: 15px;
  width: 90%; }

.user-info span::after {
  content: url("edit.png"); }

#fewords {
  margin: auto;
  display: inline;
  margin-right: 38%; }

.description {
  position: absolute;
  right: -1%;
  margin-top: 3%; }

textarea {
  border-radius: 5%;
  border: 2px dashed black; }

.snip1493 {
  font-family: 'Open Sans', Arial, sans-serif;
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 150px;
  max-width: 310px;
  width: 100%;
  background-color: #ffffff;
  color: #000000;
  text-align: left;
  font-size: 16px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15); }

.snip1493 * {
  box-sizing: border-box; }

.snip1493:after {
  position: absolute;
  top: 12px;
  left: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 25px 0 25px 25px;
  border-color: transparent transparent transparent #d2652d;
  content: '';
  transform: translateX(-100%);
  transition: all 0.3s ease; }

.snip1493 img {
  margin: 0;
  max-width: 100%;
  vertical-align: top;
  position: relative; }

.snip1493 figcaption {
  padding: 20px 20px 20px;
  background-color: #ffffff; }

.snip1493 .date {
  background-color: #d2652d;
  top: 15px;
  color: #fff;
  right: 15px;
  min-height: 48px;
  min-width: 48px;
  position: absolute;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 50%;
  padding: 10px 0; }

.snip1493 .date span {
  display: block;
  line-height: 14px; }

.snip1493 .date .month {
  font-size: 11px; }

.snip1493 h3,
.snip1493 p {
  margin: 0;
  padding: 0; }

.snip1493 h3 {
  margin-bottom: 10px;
  display: inline-block;
  font-weight: 700; }

.snip1493 p {
  font-size: 0.8em;
  margin-bottom: 10px;
  line-height: 1.6em; }

.snip1493 a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1; }

.snip1493:hover:after,
.snip1493.hover:after {
  transform: translateX(0%); }

.created {
  position: relative;
  margin-top: 6%; }

.figures {
  margin-left: 11%; }

.glyphicon {
  position: absolute;
  display: inline;
  font-size: 120px;
  top: 40%; }

.glyphicon:hover {
  transition: all 0.1s ease-in-out;
  cursor: pointer; }

.glyphicon-menu-right:hover {
  transform: translate(20px, 0); }

.glyphicon-menu-left:hover {
  transform: translate(-20px, 0); }

@media (max-width: 1300px) {
  .last, .description {
    display: none; }
  .user-info {
    width: 50%;
    top: 2%;
    margin-left: 15%; }
  #main-photo {
    width: 40%; }
  .snip1493:first-child {
    margin-right: 10%; } }

@media (max-width: 1000px) {
  .glyphicon {
    display: none; } }

@media (max-width: 870px) {
  .snip1493 {
    margin-left: -5%; } }

@media (max-width: 768px) {
  .middle {
    display: none; }
  .snip1493 {
    margin: 0 0 0 15%; } }

@media (max-width: 666px) {
  .rating-box {
    display: none; }
  #main-photo {
    width: 95%; }
  .user-info {
    margin: 65% 0 0 -30%;
    width: 95%; } }
